import Link from 'next/link'
import { Product } from '@/lib/api'

interface ProductCardProps {
  product: Product
}

export default function ProductCard({ product }: ProductCardProps) {
  return (
    <Link href={`/products/${product.id}`}>
      <div className="bg-white rounded-lg shadow-md overflow-hidden hover:shadow-lg transition-shadow duration-300 group cursor-pointer h-full flex flex-col">
        {/* Image container - using regular img tag instead of Next.js Image */}
        <div className="h-48 w-full bg-gray-100 flex-shrink-0 flex items-center justify-center p-4">
          <img
            src={product.image}
            alt={product.title}
            className="max-h-full max-w-full object-contain group-hover:scale-105 transition-transform duration-300"
            loading="lazy"
          />
        </div>
        <div className="p-4 flex-grow flex flex-col">
          <h3 className="font-semibold text-lg mb-2 line-clamp-2">
            {product.title}
          </h3>
          <div className="mt-auto">
            <div className="flex justify-between items-center">
              <span className="text-2xl font-bold text-blue-600">
                ${product.price.toFixed(2)}
              </span>
              <div className="flex items-center">
                <span className="text-yellow-400">â˜…</span>
                <span className="ml-1 text-gray-600">
                  {product.rating.rate} ({product.rating.count})
                </span>
              </div>
            </div>
            <p className="text-gray-500 text-sm mt-2 line-clamp-1 capitalize">
              {product.category}
            </p>
          </div>
        </div>
      </div>
    </Link>
  )
}